.container {
    width: 1000px;
    min-height: 600px;
    margin-top: 20px;

    .title {
        text-align: center;
        font-size: 25px;
    }

    form {
        display: grid;
        place-items: center;
        gap: 10%;
        grid-template-columns: 1fr;

        .info {
            width: 500px;
            height: 30px;
            background-color: rgba(234, 196, 219, 0.132);
            outline: none;
            border: 0;
            border-bottom: 2px solid rgba(121, 13, 78, 0.356);
            border-radius: 7px;
        }

        .message {
            width: 500px;
            height: 250px;
            resize: none;
            background-color: rgba(234, 196, 219, 0.132);
            outline: none;
            border: 0;
            border-bottom: 2px solid rgba(121, 13, 78, 0.356);
            border-radius: 7px;
        }

        .button {
            width: 100px;
            height: 30px;
            color: rgb(255, 255, 255);
            background-color: rgba(121, 13, 78, 0.356);
            outline: none;
            border: 0;
            border-radius: 3px;
        }
    }
}

@media screen and (min-width: 780px) and (max-width: 1000px) {
    .container {
        width: 97vw;
        height: 500px;
        // background-color: rgb(0, 236, 134);
    }
}

